import React from 'react'
import {Card, Image, Icon, Divider, Grid, List} from 'semantic-ui-react'

import 'semantic-ui-css/semantic.min.css';

import chenyan from "../../assets/images/avatar/chenyan.jpg"
import liyuanling from "../../assets/images/avatar/liyuaning.jpg"
import shenqun from "../../assets/images/avatar/shenqun.png"
import stuart from "../../assets/images/avatar/Stuart.jpeg"
import yangluwei from "../../assets/images/avatar/yangluwei.png"

import { Segment } from 'semantic-ui-react'

const images = [chenyan, liyuanling, shenqun, stuart, yangluwei]
const names = ['陈燕', '赵启泰', '沈军', 'Stuart Burgess', '杨鲁伟']
const orgs = ['中国科学研究院', '中国农业大学', '南京大学', 'University of Bristol', '中国科学院']
const industries = [['电气自动化', '建筑建材'], ['食品饮料'], ['建筑材料', '家居用品'], ['Design of mechanical systems'], ['低温制冷技术', '热工水力']]

class CardImage extends React.Component {

    constructor() {
        super();
        this.state = {
            language:localStorage.getItem('language')
        }
    }

    render() {
        return (
            <div>
                <Segment placeholder>
                    <Divider horizontal style={{textAlign:'center', fontSize:'40px', color:'#6258b5', }}>{this.state.language === '中文' ? '行业专家介绍': 'Expert Information'}</Divider>
                    <Grid container stackable verticalAlign='middle'>
                        <Grid.Row>
                            <Grid.Column textAlign='right'>

                                <a href='/expect' style={{textAlign:'right', color:'#6258b5'}}>{this.state.language === '中文' ? '更多专家': 'More'}<Icon name='angle double right' /></a>

                            </Grid.Column>
                        </Grid.Row>

                        <Grid.Row>
                            <Card.Group centered itemsPerRow={5}>
                                {
                                    names.map((value, index) => {
                                        return (
                                            <Card as='a' href='/expect' key={index}>
                                                <Card.Content>
                                                    <Image src={images[index]} size='medium' circular style={{width:'200px', height:'240px'}}/>
                                                    <Card.Header style={{textAlign:'center'}}>{value}</Card.Header>
                                                    <Card.Meta style={{textAlign:'center'}}>
                                                        <span className='date'>{orgs[index]}</span>
                                                    </Card.Meta>
                                                    <Card.Description>
                                                        <List bulleted>
                                                            {
                                                                industries[index].map((value, index) => {
                                                                    return (
                                                                        <List.Item key={index}>{value}</List.Item>
                                                                    )
                                                                })
                                                            }
                                                        </List>
                                                    </Card.Description>
                                                </Card.Content>
                                            </Card>
                                        )
                                    })
                                }
                            </Card.Group>
                        </Grid.Row>
                    </Grid>

                </Segment>
            </div>
        );
    }
}

export default CardImage;
