import React from 'react';

import {Segment} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';


import 'semantic-ui-css/semantic.min.css';
import AntdTable from "./table/AntdTable";

import { Input } from 'antd';
import 'antd/dist/antd.css';

import TableData from './table/TableData'

class IndustryConsumer0 extends React.Component {
    constructor() {
        super()
        this.state = {
            items: TableData.indexs.map((i) => {
                return {
                    key:i + 1,
                    id: i + 1,
                    name: TableData.techNames[i],
                    techConsumer: TableData.techConsumers[i],
                    techPrice: TableData.techPrices[i],
                    techDate: TableData.techDates[i],
                }
            })
        }
    }

    render() {
        return (
            <Segment>
                <Input.Search ref='input' placeholder="可输入任意信息进行搜索"
                              onSearch={this.onSearch}
                              enterButton
                />
                <AntdTable dataSource={this.state.items} />
            </Segment>
        )
    }

    onSearch = (value, event) => {
        console.log(value)
        var res = []
        for (var i=0;i<TableData.ids.length;i++) {
            if(TableData.ids[i].toString().includes(value) || TableData.techNames[i].toString().includes(value) || TableData.techTypes[i].toString().includes(value) || TableData.techSources[i].toString().includes(value) || TableData.techDates[i].toString().includes(value)) {
                res.push({
                    key:i + 1,
                    id: i + 1,
                    name: TableData.techNames[i],
                    techConsumer: TableData.techConsumers[i],
                    techPrice: TableData.techPrices[i],
                    techDate: TableData.techDates[i],
                })
            }
        }
        console.log(res)
        this.setState({
            items: res
        })
    }
}

export default IndustryConsumer0
