import React from 'react'
import { Segment, Grid, Header, List, Container, Statistic, Image} from 'semantic-ui-react'

import 'semantic-ui-css/semantic.min.css';

import technology from './technology_count.jpg'

class ComposeFooter extends React.Component {

    tick = () => {
        // console.log('timer ', this.state.techNum)
        localStorage.setItem('techNum', parseInt(localStorage.getItem('techNum')) + 1)
        this.setState({
            language:localStorage.getItem('language'),
            techNum:parseInt(localStorage.getItem('techNum'))
        })

    }

    componentDidMount() {
        this.timer = setInterval(this.tick, 2000);
    }
    componentWillUnmount() {
        //在unmount回调清除定时器
        clearInterval(this.timer);
    }

    constructor() {
        super();
        if(localStorage.getItem('techNum') == null) {
            localStorage.setItem('techNum', 18300)
        }
        this.state = {
            language:localStorage.getItem('language'),
            techNum:parseInt(localStorage.getItem('techNum'))
        }

    }
    render() {
        return (
            <Segment inverted vertical style={{ padding: '5em 0em' }}>
      <Container>
        <Grid  inverted stackable>
          <Grid.Row>
            <Grid.Column width={2}>
              <Header inverted as='h4' content={this.state.language === '中文' ? '了解我们': 'About Us'} />
              <List link inverted>
                  <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '成长历程': 'Development History'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '机构文化': 'Organization Culture'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '工作机会': 'Career Opportunity'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '新闻事件': 'Company News'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '联系我们': 'Contact Us'}</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={2}>
              <Header inverted as='h4' content={this.state.language === '中文' ? '合作方法': 'Partnership Programs'} />
              <List link inverted>
                <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '全球成员': 'Global Partner'}</List.Item>
                <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '地区成员': 'Regional Partner'}</List.Item>
                <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '年度成员': 'Yearly Partner'}</List.Item>
                <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '定制服务': 'Customized Program'}</List.Item>
              </List>
            </Grid.Column>
              <Grid.Column width={2}>
                  <Header inverted as='h4' content={this.state.language === '中文' ? '账户管理': 'Account Management'} />
                  <List link inverted>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '登陆问题': 'Login Problems'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '信息更改': 'Profile Update'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '更多建议': 'More Suggestions'}</List.Item>
                      {/*<List.Item as='a'>定制服务</List.Item>*/}
                  </List>
              </Grid.Column>
              <Grid.Column width={4}>
                  <Header inverted as='h4' content={this.state.language === '中文' ? '合作机构': 'Development History'} />
                  <List link inverted>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '世界工业和技术研究组织协会': 'WAITRO'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '江苏省产业技术研究院': 'JITRI'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '浙江工业大学': 'ZJUT'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '中国农业大学': 'CAU'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '中文' ? '中国科学院': 'CAS'}</List.Item>
                  </List>
              </Grid.Column>
              <Grid.Column width={3}>
                  <br/><br/><br/>
                  <Statistic inverted size='small' color='blue'>
                      <Statistic.Value color='blue'>

                          <span style={{color:'violet'}}>{parseInt(this.state.techNum / 10000)}</span>
                          <span style={{color:'orange'}}>{parseInt(this.state.techNum / 1000 % 10)}</span>
                          <span style={{color:'yellow'}}>{parseInt(this.state.techNum / 100 % 10)}</span>
                          <span style={{color:'teal'}}>{parseInt(this.state.techNum / 10 % 10)}</span>
                          <span style={{color:'yellow'}}>{parseInt(this.state.techNum  % 10)}</span>

                      </Statistic.Value>
                      {this.state.language === '中文' ? <Statistic.Label>个技术已接入<span style={{fontSize:'20px'}}>我们</span><span style={{fontSize:'22px'}}>平</span><span style={{fontSize:'25px'}}>台</span></Statistic.Label>
                          : <Statistic.Label><span style={{fontSize:'5px'}}>pieces   of   technology   access      </span><span style={{fontSize:'15px', color:'yellow'}}>Our&nbsp;platform</span><span style={{fontSize:'15px'}}></span></Statistic.Label>}

                  </Statistic>
              </Grid.Column>
              <Grid.Column width={3}>
              <Image src={technology}  size='medium' inline/>
              </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </Segment>
        );
    }
}

export default ComposeFooter;
