import React from 'react'
import _ from 'lodash'


import { Menu, Icon } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'

const overlayStyle = {
    float: 'right',
    margin: '0em 3em 1em 0em',
}

const fixedOverlayStyle = {
    ...overlayStyle,
    position: 'fixed',
    top: '80px',
    zIndex: 10,
}

const overlayMenuStyle = {
    position: 'relative',
    right: 0,
    transition: 'right 0.5s ease',
}

const fixedOverlayMenuStyle = {
    ...overlayMenuStyle,
    left: '1310px',
    top: '250px',
    // float: 'right',
}

class FloatMenu extends React.Component {
    constructor() {
        super()
        this.state = {
            menuFixed: true,
            overlayFixed: true,
        }
    }
    render() {
        const {  overlayFixed } = this.state
        return (
            <div ref={this.handleOverlayRef} style={overlayFixed ? fixedOverlayStyle : overlayStyle}>
                <Menu
                    icon='labeled'
                    style={true ? fixedOverlayMenuStyle : overlayMenuStyle}
                    vertical
                    fixed='right'
                >
                    <Menu.Item as="a" href="/" position='left'>
                        <span>
                        <Icon name='text telephone'/>
                        Call&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        </span>
                    </Menu.Item>

                    <Menu.Item as="a" href="/" position='left'>
                        <span>
                        <Icon name='mail' />
                        Email&nbsp;
                        </span>
                    </Menu.Item>

                    <Menu.Item as="a" href="/" position='left'>
                        <span>
                        <Icon name='share square' />
                        Share
                        </span>
                    </Menu.Item>
                </Menu>
            </div>
        )
    }
    handleOverlayRef = (c) => {
        const { overlayRect } = this.state

        if (!overlayRect) {
            this.setState({ overlayRect: _.pick(c.getBoundingClientRect(), 'height', 'width') })
        }
    }
}

export default FloatMenu
