import React from 'react'

import { Table } from 'antd';
import 'antd/dist/antd.css';
import './AntdTable.css'

import TableData from './TableData'


function getIndex(name) {
    for(var i=0; i<TableData.techNames.length; i++) {
        if(TableData.techNames[i] === name) {
            return i + 1
        }
    }
}

const columns = [{
    title: '序号',
    dataIndex: 'id',
    key: 'id',
    render: text => <a href={"/industry_provider/" + text }>{text}</a>,
}, {
    title: '技术名称',
    dataIndex: 'name',
    key: 'name',
    render: (text, record, index) => <a href={`/industry_provider/${getIndex(text)}`} style={{fontColor:'red'}}>{text}</a>,
}, {
    title: '技术类型',
    dataIndex: 'techType',
    key: 'techType',
}, {
    title: '来源',
    dataIndex: 'techSrc',
    key: 'techSrc',
}, {
    title: '日期',
    dataIndex: 'techDate',
    key: 'techDate',
}];



class AntdTable extends React.Component {
    // constructor(props) {
    //     super(props)
    // }
    render() {
        return (
            <Table dataSource={this.props.dataSource} columns={columns} OnCell={this.onRow}/>
        )
    }
    onRow = (record, index) => {
        console.log(record, index)
        return {
            onClick: (record, index) => {
                console.log(record, index)
            }
        }
    }
}

export default AntdTable
