import React, { Component } from 'react';

import 'semantic-ui-css/semantic.min.css';

import './App.css'

import { BrowserRouter as Router, Route, Switch,  } from 'react-router-dom';
import Home from './component/home/Home'

import {Segment, Sidebar, Menu, Icon, Input, Container, Dropdown} from 'semantic-ui-react'
import LoginForm from "./component/login/LoginForm";
import IndustryProvider from './component/industry_provider/IndustryProvider';
import IndustryConsumer from './component/industry_consumer/IndustryConsumer';

import logo from "./assets/images/big_logo.png";

import Badge from '@material-ui/core/Badge';
import MailIcon from '@material-ui/icons/Mail';

import ProviderSiderMenu1 from './component/industry_provider/ProviderSiderMenu1'
import ConsumerSiderMenu1 from './component/industry_consumer/ConsumerSiderMenu1'

import Expect from "./component/test/Expect";

class App extends Component {
    constructor(props) {
        super(props)
        if(localStorage.getItem('language') == null) {
            localStorage.setItem('language', '中文')
        }
        this.state = {
            handled: false,
            visible: false,
            language: localStorage.getItem('language'),
            username: localStorage.getItem('username')
        }
    }

    runMethod = () => {
        console.log('app run method ', this.state.visible)
        if(this.state.handled === false) {
            this.setState({
                visible: true,
                handled: false
            })
        } else {
            this.setState({
                visible: this.state.visible,
                handled: false
            })
        }
    }

    onClick = (event, data) => {
        console.log('data ', data)
        console.log('data object ', data.children)
        localStorage.setItem('language', data.children)
        console.log('language', localStorage.getItem('language'))
        this.setState({
            visible: this.state.visible,
            language:data.children
        })
    }

    handleSidebarHide = (event, data) => {
        console.log('event ', event)

        console.log('className                              ', event.path[0].className)
        console.log('innerText                              ', event.path[0].innerText)

        console.log('data ', data)

        console.log('height ', document.body.clientHeight)

        if(event.path[0].innerText === '菜单' || event.path[1].innerText === '菜单' || event.path[0].innerText === 'Menu' || event.path[1].innerText === 'Menu') {
            this.setState({visible: false, handled: true})
        } else {
            this.setState({visible: false, handled: false})
        }

    }

    render() {
          return (
              <div>

                  <Segment basic>

                      <Menu fixed='top' inverted size='huge'>


                          <Menu.Item as='a' header href="/" color='black'>
                              <img  src={logo} style={{ marginRight: '1.5em', height:'50px', width:'50px' }} alt=""/>
                              <p style={{fontSize:'20px', font:'chivo'}}>{this.state.language === '中文' ? '全球领先产业技术交易平台' : 'World Leading Technology Exchange Platform'}</p>
                          </Menu.Item>



                          <Container>
                              <Menu.Menu position='right'>

                                  <Menu.Item >
                                      <Input className='icon' icon='search' placeholder='Search...' />
                                  </Menu.Item>

                                  <Menu.Item onClick={this.runMethod}>
                                      <Icon name='sort amount down'/>
                                      {this.state.language === '中文' ? '菜单' : 'Menu'}
                                  </Menu.Item>

                                  {/*<Menu.Item onClick={this.runMethod}>*/}
                                      {/*<Icon name='globe'/>*/}
                                      {/*语言*/}
                                  {/*</Menu.Item>*/}

                                  {/*<Dropdown className='icon' item  icon='globe'  text='语言' iconPosition='left'>*/}
                                  <Dropdown item  trigger={<span><Icon name='globe' />{this.state.language === '中文' ? '语言' : 'Language'}</span>} icon={null}>
                                      <Dropdown.Menu>
                                          <Dropdown.Item as="a" href="/" onClick={this.onClick}>{this.state.language === '中文' ? '中文' : '中文'}</Dropdown.Item>
                                          <Dropdown.Item as="a" href="/" onClick={this.onClick}>{this.state.language === '中文' ? 'English' : 'English'}</Dropdown.Item>
                                      </Dropdown.Menu>
                                  </Dropdown>

                                  <Menu.Item as='a' href='/login'>
                                      <Badge badgeContent={6} color="secondary">
                                          <MailIcon />
                                      </Badge>
                                  </Menu.Item>

                                  <Menu.Item as='a' href="/login">
                                      <Icon name='add user'/>
                                      {this.state.username != null ? this.state.username : (this.state.language === '中文' ? '登录' : 'Login')}
                                  </Menu.Item>
                              </Menu.Menu>
                          </Container>
                      </Menu>
                  </Segment>


                  <Sidebar.Pushable >

                      <Sidebar
                          as={Menu}
                          animation='slide along'
                          icon='labeled'
                          inverted
                          onHide={this.handleSidebarHide}
                          vertical
                          visible={this.state.visible}
                          width='wide'
                          className='mySidebar'
                          id='mySidebar'
                          style={{height:'100px'}}
                      >
                          <Menu.Item as='a' href='/' />
                          <Menu.Item as='a' href='/industry_provider' icon='file alternate' content={this.state.language === '中文' ? '行业技术提供' : 'Technology Provide'} />
                          <Menu.Item as='a' href='/industry_consumer' icon='file alternate outline' content={this.state.language === '中文' ? '行业技术需求' : 'Technology Request'} />
                          <Menu.Item as='a' icon='graduation cap' content={this.state.language === '中文' ? '专家智库' : 'Talent Pool'} />
                          <Menu.Item as='a' icon='wrench' content={this.state.language === '中文' ? '技能培训' : 'Training Program'} />
                          <Menu.Item as='a' icon='cubes' content={this.state.language === '中文' ? '服务支持' : 'Service & Support'} />
                          <Menu.Item as='a' icon='info circle' content={this.state.language === '中文' ? '活动咨询' : 'Information'} />
                          <Menu.Item as='a' icon='cloud download' content={this.state.language === '中文' ? '材料下载' : 'Download'} />
                          <Menu.Item as='a' icon='university' content={this.state.language === '中文' ? '政策检索' : 'Policy Seek'} />
                          <Menu.Item as='a' icon='gem outline' content={this.state.language === '中文' ? '会员服务' : 'Membership'} />
                          <Menu.Item as='a' icon='handshake outline' content={this.state.language === '中文' ? '合作方式' : 'Partner Programs'} />
                      </Sidebar>

                      <Sidebar.Pusher className='myPusher' dimmed={this.state.visible}>





                          <Router>
                              <Switch>
                                  <Route exact path="/" component={Home}/>
                                  <Route exact path="/login" component={LoginForm}/>
                                  <Route exact path="/industry_provider" component={IndustryProvider}/>
                                  <Route exact path="/industry_provider/:id" component={ProviderSiderMenu1}/>

                                  <Route exact path="/industry_consumer" component={IndustryConsumer}/>
                                  <Route exact path="/industry_consumer/:id" component={ConsumerSiderMenu1}/>

                                  <Route exact path="/expect" component={Expect}/>

                              </Switch>
                          </Router>

                      </Sidebar.Pusher>
                  </Sidebar.Pushable>
              </div>
          );

      }
}

export default App;
